a {
  text-decoration: none;
  color: #E0ECFF;
}
* {
  box-sizing: border-box;
}
.footer {
  font-weight: 500;
}
.footer .hr {
  width: 91.5%;
  height: 1px;
  max-width: 800px;
  margin: 0 auto;
  background: linear-gradient(to right, rgba(255, 0, 0, 0), rgba(255, 255, 255, 0.5), rgba(255, 0, 0, 0));
}
.footer-box {
  display: flex;
  justify-content: space-between;
  width: 1063px;
  height: 50px;
  margin: 0 auto;
  margin-bottom: 32px;
  align-items: flex-end;
}
.footer-box .left a {
  margin-left: 24px;
  color: #93A1B8;
}
.footer-box .left .one-a {
  margin-left: 0;
}
.footer-box .contact {
  display: flex;
  justify-content: between-space;
  align-items: center;
}
.footer-box .contact img {
  margin-left: 16px;
}
.footer-box .mobile {
  display: none;
}
@media only screen and (min-width: 1140px) and (max-width: 1568px) {
  .footer-box {
    width: 1013px;
  }
}
@media only screen and (min-width: 760px) and (max-width: 1140px) {
  .footer-box {
    width: 712px;
  }
}
@media only screen and (max-width: 759px) {
  .footer-box {
    width: 100%;
    min-width: 375px;
    padding: 0 24px;
    height: 71px;
    display: block;
  }
  .footer-box .pc-bn,
  .footer-box .pc-con {
    display: none;
  }
  .footer-box .left {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 27px;
  }
  .footer-box .mobile {
    display: flex;
    justify-content: space-between;
    margin-top: 24px;
  }
}
